// resume-img
$resume-img-width:                  20rem;
$resume-img-height:                 $resume-img-width * 4 / 3;
$resume-img-border:                 5px;
$resume-img-color:                  #d0d0d0;


// nav-list
$nav-list-color:                    #9b9b9b;
$nav-list-active:                   $gray-dark;


// layout
$layout-gutter:                     0 1rem;


// nav-sidebar
$nav-sidebar-color:                 #7a7a7a;

$border-color:                      #f0f0f0;

// navbar
$navbar-classic-y:                  .625rem;
$navbar-classic-form-padding:       .34375rem;
$navbar-classic-height:             1.875rem;
$navbar-classic-size:               .9375rem;


